.btn {
  overflow: hidden;
  text-decoration: none;
  display: inline-block;
  font-family: "Roboto Condensed", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: transparent;
  padding-top: 14px;
  padding-right: 27px;
  padding-bottom: 14px;
  padding-left: 27px;
  border: 1px solid #ffffff;
  border-radius: 5px;
  cursor: pointer; }
  .btn:hover, .btn:focus {
    color: #ffffff;
    background-color: #00b2ff; }
  .btn:active {
    color: #7c7c7c; }

.main-nav {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  padding-left: 122px; }

.main-nav__btn {
  margin-left: 49px; }

.main-nav__telephone,
.main-nav__email {
  overflow: hidden;
  text-decoration: none;
  display: flex;
  align-items: center;
  color: #ffffff;
  font-family: "PTMono", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px; }

.main-nav__telephone {
  margin-left: 86px; }

.main-nav__phone-svg {
  margin-right: 15px; }

.main-nav__email-svg {
  margin-right: 20px; }

.main-nav__toggle {
  position: relative;
  margin-right: auto;
  width: 30px;
  height: 30px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer; }
  .main-nav__toggle::before, .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 18px;
    right: 0;
    width: 37px;
    height: 1px;
    background-color: #ffffff; }
    .main-nav__toggle::before:active::before, .main-nav__toggle::after:active::before {
      background-color: rgba(255, 255, 255, 0.3); }
  .main-nav__toggle::after {
    top: 28px; }

@media (max-width: 1200px) {
  .main-nav {
    padding-left: 50px; } }

@media (max-width: 1023px) {
  .main-nav__btn {
    display: none; }
