  .main-nav__email {
    font-size: 0; }
  .main-nav__telephone {
    margin-left: 18px; }
  .main-nav__email {
    margin-left: 39px; }
  .main-nav__phone-svg {
    margin-right: 0; }
  .main-nav__email-svg {
    margin-right: 20px; }
  .main-nav__toggle {
    margin-right: 0; }
    .main-nav__toggle::before, .main-nav__toggle::after {
      top: 7px; }
    .main-nav__toggle::after {
      top: 19px; } }

@media (max-width: 767px) {
  .main-nav__telephone {
    margin-left: 8px; }
  .main-nav__email {
    margin-left: 30px; } }

